/* Timeline.css */

.timeline {
    position: relative;
    margin: 50px 0;
  }
.timeline-section{
    margin-top: 100px;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    background-color: transparent;
}
  
  .timeline:before {
    content: '';
    position: absolute;
    left: 70%;
    top: 0;
    width: 2px;
    height: 100%;
    background: #444444;
    -webkit-animation: lineFade 1s ease-in-out;
            animation: lineFade 1s ease-in-out;
  }
  
  .timeline-item {
    position: relative;
    margin-bottom: 20px;
    -webkit-animation: itemFade 1s ease-in-out;
            animation: itemFade 1s ease-in-out;
  }
  
  .timeline-content {
    position: relative;
    display: flex;
    width: 60%;
    margin-left: 5%;
    padding: 10px;
    background: #2424247a;
    color: #fff;
    border-radius: 10px;
    
    box-shadow: 0 0 10px rgba(110, 110, 110, 0.3);
    -webkit-animation: contentFade 1s ease-in-out;
            animation: contentFade 1s ease-in-out;
  }
  
  .timeline-content h3 {
    margin-top: 0;
    color: #ff7f00;
  }
  
  .timeline-dot {
    position: absolute;
    left: 70%;
    top: 10px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 15px;
    height: 15px;
    border-radius: 50%;
    -webkit-animation: dotFade 1s ease-in-out;
            animation: dotFade 1s ease-in-out;
  }
  .org-logo{
    width: 18vh;
    height: 18vh;
    border-radius: 50%;
    background-color: #fff;
    content: icon;
    margin-right: 5vh;
  }
  
  .orange-dot {
    background-color: #ff7f00;
  }
  
  .black-dot {
    background-color: #222;
  }
  .timeline-date{
    background-color: transparent;
    color: #fff;
    position: absolute;
    left: 80%;
  }
  
  @-webkit-keyframes lineFade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes lineFade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @-webkit-keyframes itemFade {
    from {
      opacity: 0;
      -webkit-transform: translateY(50px);
              transform: translateY(50px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  
  @keyframes itemFade {
    from {
      opacity: 0;
      -webkit-transform: translateY(50px);
              transform: translateY(50px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  
  @-webkit-keyframes contentFade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @keyframes contentFade {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
  
  @-webkit-keyframes dotFade {
    from {
      opacity: 0;
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    to {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
  @keyframes dotFade {
    from {
      opacity: 0;
      -webkit-transform: scale(0);
              transform: scale(0);
    }
    to {
      opacity: 1;
      -webkit-transform: scale(1);
              transform: scale(1);
    }
  }
  
